import React, {memo, useEffect, useState} from "react";
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
import "react-perfect-scrollbar/dist/css/styles.css";
import PerfectScrollbar from "react-perfect-scrollbar";
import {NavLink} from "react-router-dom";
import MessageBox from "./MessageBox/MessageBox";
import $ from "jquery";
// get all const type => doctor_id for set in localstorage
import {DOCTOR_ID} from './../../../../services/Authentication/Types'
import useChat from "./_useChat";
import {useSelector, useDispatch} from "react-redux";

export default memo((props) => {
    const idUser = props.userId;
    const [joinData, setJoinData] = useState({
        otherUserId: "603a1ff192bab419604ccd35",
        otherUserName: "test",
    });
    const [messages, setMessages] = useState();
    const dispatch = useDispatch();
    const {setJoinToChat, sendMessage, getMessage} = useChat();
    const state = useSelector((state) => state);
    useEffect(() => {
        setJoinData({
            ...joinData,
            thisUserId: "603c996cdaba977f38e970cd",
            thisUserName: state.Doctor.GetMe.data ? state.Doctor.GetMe.data.data.fullName : '',
        });
        setMessages({
            ...messages,
            sender: "603a1ff192bab419604ccd35"
        })
        setJoinToChat(joinData);
        getMessage();
    }, []);
    return (
        <div>
            <div id="loader-wrapper">
                <div id="loader"></div>
                <div className="loader-section section-left"></div>
                <div className="loader-section section-right"></div>
            </div>
            <Header/>
            <div id="main">
                <div className="wrapper">
                    <SideBar/>
                    <div id="back_shado"></div>
                    <div className="container">
                        <div className="row">
                            <div className="col s12">
                                <div id="breadcrumbs-wrapper">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col s10 m6 l6">
                                                <h5 className="breadcrumbs-title">Chat</h5>
                                                <ol className="breadcrumbs">
                                                    <li>
                                                        <NavLink to="/admin-top">
                                                            <a>Dashboard</a>
                                                        </NavLink>
                                                    </li>
                                                    <li>
                                                        <a className="active">Chat</a>
                                                    </li>
                                                </ol>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className={"col l4 s12"}>
                                <div className={"profile-detail"}>
                                    <div className={"image-pro-chat"}>
                                        <img src={"/img/avatar/avatar-11.png"} className={"responsive-img circle"}/>
                                    </div>
                                    <p>User Name</p>
                                    <p style={{color : '#22e6ff' , fontSize : '13px' , margin : '0px !important'}}>symptom</p>

                                    <div className={"col s12"}>
                                        <div className={"row"} style={{display : 'flex' ,justifyItems : 'center' ,margin : '15px' , flexDirection : 'row' }}>
                                            <a style={{textTransform : 'unset'}} className={"btn waves-effect blue darken-4"}>
                                                More Detail
                                            </a>

                                            <a style={{textTransform : 'unset' , marginLeft : '5px'}} className={"btn waves-effect red darken-4"}>
                                                Delete Chat
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col l8 s12" style={{marginBottom: 30}}>
                                <div className="chat_box">
                                    <div className="col s12">
                                        <div className="row">
                                            <div className="col s12" style={{padding: 0}}>
                                                <div className="chat_page_content">
                                                    <div id="top_header_one_contact">
                                                        <div className="col s12">
                                                            <img
                                                                src="/img/avatar/avatar-17.png"
                                                                className="circle responsive-img"
                                                            />

                                                            <span id="contact_typing">Baran Samadi</span>
                                                            <br/>
                                                            <div style={{float: "left", width: "90%"}}>
                                                                <span id="online_Check">online</span>
                                                            </div>

                                                            <div style={{position: "absolute", right: 20}}>
                                <span id="top_menu_act">
                                  <i className="material-icons">delete</i>
                                </span>
                                                                <span id="top_menu_act">
                                  <i className="material-icons">info_outline</i>
                                </span>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div className="main_chat_data">
                                                        <div
                                                            id="you"
                                                            className="col s12"
                                                            style={{marginTop: 15, float: "right"}}
                                                        >
                                                            <div style={{position: "relative"}}>
                                                                <img
                                                                    src="/img/avatar/avatar-10.png"
                                                                    className="circle responsive-img"
                                                                />

                                                                <span id="you_write">you</span>
                                                            </div>
                                                            <ul
                                                                className="col s12"
                                                                id="text_you"
                                                                style={{width: "80%"}}
                                                            >
                                                                <li>
                                                                    <p>
                                                                        this is a new message for test all of
                                                                        products are you ok man and you are very
                                                                        nice for me and youve{" "}
                                                                    </p>
                                                                    <br/>
                                                                    <span id="text_det_you_date_done">
                                    <i className="material-icons">done_all</i>
                                    <span id="time">12:55</span>{" "}
                                  </span>
                                                                </li>
                                                                <br/>

                                                                <li>
                                                                    <p>this is a text </p>
                                                                    <br/>
                                                                    <span id="text_det_you_date_done">
                                    <i className="material-icons">done</i>
                                    <span id="time">13:55</span>{" "}
                                  </span>
                                                                </li>
                                                                <br/>
                                                            </ul>
                                                        </div>

                                                        <div id="user" className="col s12">
                                                            <div style={{position: "relative"}}>
                                                                <img
                                                                    src="/img/avatar/avatar-17.png"
                                                                    className="circle responsive-img"
                                                                />

                                                                <span id="user_write">Baran</span>
                                                            </div>
                                                            <ul
                                                                className="col s12"
                                                                id="text_user"
                                                                style={{width: "80%"}}
                                                            >
                                                                <li>
                                                                    <p style={{margin: 3}}>
                                                                        this is a new message for test all of
                                                                        products are you ok man and you are very
                                                                        nice for me and youve{" "}
                                                                    </p>
                                                                    <span id="text_det_you_date_done">
                                    <span id="time">12:55</span>
                                  </span>
                                                                </li>
                                                                <br/>

                                                                <li>
                                                                    <p style={{margin: 3}}>thank you </p>
                                                                    <span id="text_det_you_date_done">
                                    <span id="time">12:55</span>
                                  </span>
                                                                </li>
                                                                <br/>
                                                            </ul>
                                                        </div>
                                                    </div>

                                                    <MessageBox
                                                        onSendMessage={(message) => {
                                                            setMessages({
                                                                ...messages,
                                                                receiver: "603c996cdaba977f38e970cd",
                                                                content: message,
                                                                type: 'text'
                                                            })
                                                            setTimeout(() => {
                                                                console.log("messages : ", messages);
                                                                sendMessage(messages)
                                                            }, 200)
                                                        }}
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
});
