import {useEffect , useState , useRef} from 'react';
import soketIOClient from 'socket.io-client';
import {SOCKET_URL} from './../../../../services/Connection/Types';
const useChat = () => {
    const socketRef = useRef();
    const [message , setMessage] = useState();

    useEffect(()=>{
        socketRef.current = soketIOClient(SOCKET_URL);
        socketRef.current.on("connect" , () => {
            console.log("connect");
        })
    



        // socketRef.current.on("message" , )


        return () => {
            socketRef.current.disconnect();
        }


    },[])


    const sendMessage = ({data}) => {
        socketRef.current.emit("message", {data}, messageResponse => console.log("erver Response", messageResponse))
    }

    const getMessage = () => {
        socketRef.current.on("message" , reponse => console.log("get message Response", reponse))
    }


    const setJoinToChat = ({data}) => {
        socketRef.current.emit("join" , {data} , joinResponse => console.log("join is ok" , joinResponse));
    }

    return {setJoinToChat , sendMessage , getMessage}



}

export default useChat;