import { set } from 'lodash';
import React , {memo , useState} from 'react';
import $ from 'jquery';
export default memo(({onSendMessage : pushSendMessage})=>{
    
    const [message , setMessage] = useState();
    const handleMessage =() => {
        pushSendMessage(message);
        setMessage('');
        $("input[name=msg_write]").val('')
    }
    return (
        <div className="send_message">

        <div className="type_text_msg">
            <input type="text" name="msg_write"
            onChange={e => setMessage(e.target.value)}
                   placeholder="write your message text ..."/>
        </div>

        <i className="material-icons" onClick={handleMessage} style={{cursor : 'pointer'}}>send</i>
        <div id="file_selected">
                <span><i
                    className="material-icons">attachment</i></span>
            <input type="file" multiple
                   style={{opacity: 0, cursor: "pointer"}}/>
        </div>


    </div>
    )
})