import React, { memo, useEffect } from "react";
import View from "./ChatViewContainer";
import { Get_Signle_patient } from "../../../../entity/Redux/Doctors/Action";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import { GetDoctorClient } from "./../../../../entity/Redux/Doctors/Action"; // call kardan action uploaded
import $ from "jquery";
import M from "materialize-css";
export default memo((props) => {
  const dispatch = useDispatch();
  const Doctor = useSelector((state) => state);
  useEffect(() => {
    // get all doctor's clinic
    // dispatch(Get_Signle_patient());
    
    $("#loader-wrapper").fadeOut();
    if (props.match.params.id) {
      dispatch(Get_Signle_patient(props.match.params.id));
    } else {
      M.toast({ html: "please wait ..." });
    }
  }, []);

  const renderPage = () => {
    return <View userId={props.match.params.id} />;
  };
  const redirect = () => {
    return (
      <div>
        <Redirect to={"/doctor"} />
      </div>
    );
  };

  let timeSet;

  if (Doctor.Doctor.GetSinglePatient) {
    clearTimeout(timeSet);
    if (Doctor.Doctor.GetSinglePatient == true) {
      return renderPage();
    } else {
      return redirect();
    }
  } else {
    M.toast({ html: "please wait ..." });
    // timeSet = setTimeout(()=>{
    //   window.location.pathname = "/doctor";
    // } , 1000)
    return <div>please wait...</div>;
  }
});
